import { Feature } from '@app/features/types/Feature';
import { SupportedLanguages } from '../types';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

const EXTENSION = process.env.EXTENSION_CODE;
export async function getSupportedLanguages(): Promise<
  Array<SupportedLanguages>
> {
  const { featuresListLocal, featureStore } = await getLocalStorageData([
    LocalStorageKey.FeaturesListLocal,
    LocalStorageKey.FeatureStore,
  ]);
  const featuresList = {
    ...featureStore?.featuresList,
    ...featuresListLocal,
  };

  const isMultiLocaleSupported = featuresList[Feature.MultiLocalesSupport];
  if (!isMultiLocaleSupported) return ['ru'] as const;

  if (EXTENSION === 'youboost') {
    return ['ru', 'en', 'pl', 'es', 'pt', 'de', 'fr', 'tr', 'it'] as const;
  }

  return ['ru', 'en'] as const;
}
