import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { logger } from '@utils/logger';

export async function showAdPremiumBanner() {
  logger.info('youboost:Show ad premium banner');
  const { hasPremium } = await getLocalStorageData(LocalStorageKey.HasPremium);

  if (hasPremium) {
    await setLocalStorageData({ [LocalStorageKey.IsAdBannerShown]: false });
  } else {
    await setLocalStorageData({ [LocalStorageKey.IsAdBannerShown]: true });
  }
}
