import { Actions } from '@app/actions/shared';
import { browser_api } from '@app/browser_api';

export async function messageConnectionRetryToAllYoutubeTabs(): Promise<void> {
  try {
    const tabs = await browser_api.tabs.query({ url: '*://*.youtube.com/*' });
    await Promise.all([
      tabs.map((tab) => {
        if (tab.id) {
          return browser_api.tabs.sendMessage(tab.id, {
            action: Actions.NetworkConnectionRetry,
          });
        }
        return null;
      }),
    ]);
  } catch (error) {
    console.error(
      '[MESSAGE CONNECTION RETRY TO ALL YOUTUBE TABS] Error',
      error,
    );
  }
}
