import { getPACData } from '@app/storage/local/getPACData';
import { enableProxy } from '../proxyControl';

export async function updatePacScriptToLitePAC(): Promise<void> {
  console.warn('updatePacScriptToLitePAC');
  await getPACData({
    shouldRefetch: true,
    isLitePACInner: true,
  });
  await enableProxy();
}
