import { ALARM_DEPENDENT_META_API_VALUES } from '@app/alarms/constants';
import { browser_api } from '@app/browser_api';
import { MetaApiData } from '@app/storage/local/types/expirableTypes';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { exhaustiveCheck } from '@app/utils/exhaustiveCheck';
import { createOrUpdatePremiumProxyDataAlarm } from '@background/alarmHandlers/createOrUpdatePremiumProxyDataAlarm';
import { createUpdateSubscriptionInfoAlarm } from '@background/alarmHandlers/createUpdateSubscriptionInfoAlarm';
import { initGiftAlarms } from '@background/gifts/giftsOnYoutubePageBackground';
import { initSubscriptionExpiringAlarms } from '@background/subscriptionExpiring/alarms';
import { isEqual } from '@background/utils/isEqual';
import { logger } from '@utils/logger';

async function handleMetaApiValueChange(
  metaApiValue: (typeof ALARM_DEPENDENT_META_API_VALUES)[number],
) {
  switch (metaApiValue) {
    case 'updateSubscriptionInfoPeriod':
      logger.info('shared:updateSubscriptionInfoPeriodChanged');
      await createUpdateSubscriptionInfoAlarm();
      break;

    case 'proxyUpdatePeriod':
      logger.info('shared:proxyUpdatePeriodChanged');
      await createOrUpdatePremiumProxyDataAlarm();
      break;

    case 'subscriptionExpiringAlerts':
      logger.info('shared:subscriptionExpiringAlertsChanged');
      await initSubscriptionExpiringAlarms();
      break;

    case 'giftAdvertisingPeriodInMinutes':
      logger.info('shared:giftAdvertisingPeriodInMinutesChanged');
      await initGiftAlarms();
      break;

    case 'giftAdvertisingAvailableDates':
      logger.info('shared:giftAdvertisingAvailableDatesChanged');
      await initGiftAlarms();
      break;

    default:
      exhaustiveCheck(metaApiValue);
  }
}

export async function onMetaApiDataChanged(
  changes: Record<string, browser_api.Storage.StorageChange>,
) {
  const oldMetaApiData = changes[LocalStorageKey.MetaApiData]
    ?.oldValue as MetaApiData;
  const newMetaApiData = changes[LocalStorageKey.MetaApiData]
    ?.newValue as MetaApiData;

  for (const metaApiValue of ALARM_DEPENDENT_META_API_VALUES) {
    const oldValue = oldMetaApiData?.[metaApiValue];
    const newValue = newMetaApiData?.[metaApiValue];

    if (!isEqual(oldValue, newValue)) {
      logger.info(`[onMetaApiDataChanged]: Alarm-dependent metaApiValue ${metaApiValue} changed:`, {
        old: oldValue,
        new: newValue,
      });
      await handleMetaApiValueChange(metaApiValue);
    }
  }
}
