import { Alarms } from '@app/alarms/shared';
import { browser_api } from '@app/browser_api';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export class PingPongService {
  private static instance: PingPongService;
  private interval: number = process.env.PING_PONG_INTERVAL
    ? parseInt(process.env.PING_PONG_INTERVAL)
    : 500;
  private startTime?: number;
  private isInitStarted: boolean = false;
  private _isInitialized: boolean = false;

  constructor() {
    if (PingPongService.instance) {
      return PingPongService.instance;
    }

    PingPongService.instance = this;
  }

  private alarmListener = async (
    alarm: browser_api.Alarms.Alarm,
  ): Promise<void> => {
    if (alarm.name === Alarms.PingPong) {
      this.startTime = Date.now();

      await setLocalStorageData({
        [LocalStorageKey.PingPongStartTime]: this.startTime,
      });

      console.warn('PingPong alarm received');
    }
  };

  private setAlarmListener(): void {
    console.warn('PingPong setAlarmListener started');
    browser_api.alarms.onAlarm.addListener(this.alarmListener);
  }

  private removeAlarmListener(): void {
    console.warn('PingPong removeAlarmListener started');
    browser_api.alarms.onAlarm.removeListener(this.alarmListener);
  }

  public async init(): Promise<void> {
    try {
      console.warn('PingPong init started');

      if (this._isInitialized || this.isInitStarted) {
        return;
      }

      this.isInitStarted = true;

      const { pingPongStartTime } = await getLocalStorageData(
        LocalStorageKey.PingPongStartTime,
      );

      if (pingPongStartTime) {
        this.startTime = pingPongStartTime;
      }

      if (this.isAlarmListenerSet) {
        this.removeAlarmListener();
      }

      this.setAlarmListener();

      browser_api.alarms.create(Alarms.PingPong, {
        periodInMinutes: this.interval / 60000,
      });

      this._isInitialized = true;
      this.isInitStarted = false;

      console.warn('PingPong initialized');
    } catch (error) {
      console.error(error);
    }
  }

  private get isAlarmListenerSet(): boolean {
    return browser_api.alarms.onAlarm.hasListener(this.alarmListener);
  }

  public async isServiceWorkerHasSlept(): Promise<boolean> {
    try {
      console.warn('PingPong isServiceWorkerHasSlept started');
      if (!this._isInitialized) {
        await this.init();
      }

      if (!this.startTime) {
        return true;
      }

      const now = Date.now();
      const duration = now - this.startTime;

      console.warn(`Service worker slept for ${duration}ms`);

      return duration > this.interval;
    } catch (error) {
      console.error(error);
      return true;
    }
  }

  public get isInitialized(): boolean {
    return this._isInitialized;
  }
}
