import { Actions } from '@app/actions/shared';
import { browser_api } from '@app/browser_api';
import {
  type FailedVideoOpenMessage,
  isMessageOfType,
} from '@app/messages/shared';
import { setLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { temporaryPopupListenerForChangeServer } from '@background/network/temporaryPopupListenerForChangeServer';
import { openExtensionPopup } from '@background/utils/openExtensionPopup';
import { sendMessageToYoutubeTabs } from '@background/utils/sendMessageToYoutubeTabs';
import { logger } from '@utils/logger';

export async function runtimeOnMessageListener(
  message: unknown,
  { tab }: { tab?: browser_api.Tabs.Tab },
): Promise<void> {
  if (isMessageOfType(message, Actions.FailedVideoOpen) && tab?.id) {
    logger.info('runtimeOnMessageListener:Failed video open');
    browser_api.tabs.onUpdated.addListener(
      async function listener(tabIdUpdated, changeInfo) {
        if (tabIdUpdated === tab.id && changeInfo.status === 'complete') {
          await browser_api.tabs.sendMessage<FailedVideoOpenMessage>(
            tabIdUpdated,
            message,
          );
          browser_api.tabs.onUpdated.removeListener(listener);
        }
      },
    );
  }

  if (isMessageOfType(message, Actions.SaveTime) && tab?.id) {
    logger.info('runtimeOnMessageListener:Save time');
    const { time, videoId } = message.payload;

    await setLocalStorageData({
      [LocalStorageKey.SavedTime]: {
        [videoId]: Math.round(time),
      },
    });
  }

  if (isMessageOfType(message, Actions.ChangeServerToastClicked) && tab?.id) {
    try {
      if (
        browser_api.runtime.onMessage.hasListener(
          temporaryPopupListenerForChangeServer,
        )
      ) {
        browser_api.runtime.onMessage.removeListener(
          temporaryPopupListenerForChangeServer,
        );
      }

      browser_api.runtime.onMessage.addListener(
        temporaryPopupListenerForChangeServer,
      );
    } catch (error) {
      console.error('Error navigating for change server screen:', error);
    } finally {
      await openExtensionPopup();
    }
  }

  if (isMessageOfType(message, Actions.HideAllToasts) && tab?.id) {
    await sendMessageToYoutubeTabs(Actions.HideAllToasts, undefined);
  }

  if (isMessageOfType(message, Actions.HideAllToastsByType) && tab?.id) {
    await sendMessageToYoutubeTabs(Actions.HideAllToastsByType, {
      type: message.payload.type,
    });
  }
}
