import { WebRequest } from 'webextension-polyfill';

export type AdditionalDomain = {
  mode: 'DIRECT' | 'PROXY';
  domain: string;
};

export type AdditionalDomains = AdditionalDomain[];

export type BaseProxyData = {
  host: string;
  port: number;
  proxy_type?: string;
  actual_proxy_type?: string;
  auth?: string;
};

export type PremiumProxyData = Pick<BaseProxyData, 'host' | 'port' | 'auth'> & {
  proxy_type: 'vpn' | 'default';
};

export type BaseProxyDataResponse = {
  data: { proxy_server: BaseProxyData };
};

export type PremiumProxies = PremiumProxyData[];

export type PremiumProxyDataResponseError = {
  code: 'change_device' | 'subscription_expired' | 'proxy_not_found';
};

export type PremiumProxyDataResponse = {
  proxies: PremiumProxies;
  errors?: PremiumProxyDataResponseError[];
};

export type ProxyDataResponse =
  | BaseProxyDataResponse
  | PremiumProxyDataResponse;

export type ProxyCredential = {
  [key: string]: {
    userName: string;
    password: string;
    proxyType: string;
  };
};

export enum ProxyFailReason {
  ProxyNotAvailable = 'proxy-not-available',
  UserManual = 'user-manual',
}

export type ProxyAuthListenerType = (
  details: WebRequest.OnAuthRequiredDetailsType,
) => WebRequest.BlockingResponseOrPromiseOrVoid;

export type ProxyAuthListenerTypeChrome = (
  details: chrome.webRequest.WebAuthenticationChallengeDetails,
  callback?: (response: chrome.webRequest.BlockingResponse) => void,
) => void;

export type ProxyConfig = {
  pacString: string;
  proxyHost: string;
  proxyPort: number;
  vpnProxyHost?: string;
  vpnProxyPort?: number;
  shouldDecompress: boolean;
};
