import { Alarms } from '@app/alarms/shared';
import { browser_api } from '@app/browser_api';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function handleProxySetSuccess(host: string, port: number) {
  const { connectionCount, targetState } = await getLocalStorageData([
    LocalStorageKey.ConnectionCount,
    LocalStorageKey.TargetState,
  ]);
  const count = connectionCount ?? 0;
  if (targetState === 'disconnected') {
    void browser_api.alarms.create(Alarms.DisableProxy, {
      delayInMinutes: 0,
    });
  } else {
    await setLocalStorageData({
      [LocalStorageKey.CurrentState]: 'connected',
      [LocalStorageKey.ConnectionCount]: count + 1,
      [LocalStorageKey.LastProxyHost]: host,
      [LocalStorageKey.LastProxyPort]: port,
    });
  }
}
