import { getPACData } from '@app/storage/local/getPACData';
import { setLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function handleUuidUpdate(
  lastPacScriptUuid: string | undefined,
  currentUuid: string,
): Promise<void> {
  const shouldUpdate = lastPacScriptUuid !== currentUuid;
  if (shouldUpdate) {
    await setLocalStorageData({
      [LocalStorageKey.LastPacScriptUuid]: currentUuid,
    });
    await getPACData({ shouldRefetch: shouldUpdate });
  }
}
