import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import { InitI18nOptions, SupportedLanguages } from './types';
export { PRIORITY_LANGUAGE } from './constants';
export type { SupportedLanguages } from './types';
import {
  getI18nConfig,
  getLanguageFromStorage,
  setLocale,
  syncLanguageToStorage,
} from './utils';

let isInitialized = false;
let currentSupportedLngs: readonly string[] = [];

export async function initI18n(options: InitI18nOptions): Promise<typeof i18n> {
  if (isInitialized) {
    return i18n;
  }

  const { enableReact = false, debug = false, onLanguageChanged } = options;

  const { supportedLngs, resources } = await getI18nConfig();

  currentSupportedLngs = supportedLngs;

  const initialLanguage = await getLanguageFromStorage();

  if (enableReact) {
    i18n.use(initReactI18next);
  }

  i18n.init({
    lng: initialLanguage ?? supportedLngs[0],
    supportedLngs,
    fallbackLng: supportedLngs,
    debug,
    defaultNS: 'shared',
    resources,
  });

  await setLocale({
    lastSettedLocale: initialLanguage ?? null,
    currentLanguage: i18n.language as SupportedLanguages,
    changeLanguage: (language) => {
      void i18n.changeLanguage(language);
    },
  });

  i18n.on('languageChanged', (language) => {
    if (!supportedLngs.includes(language as SupportedLanguages)) return;
    void syncLanguageToStorage(language);
    void onLanguageChanged(language);
  });

  isInitialized = true;
  return i18n;
}

export function getSupportedLngsFromApp(): readonly string[] {
  return currentSupportedLngs;
}

export default i18n;
