import { Actions } from '@app/actions/shared';
import { browser_api } from '@app/browser_api';
import { isMessageOfType } from '@app/messages/shared';
import { initSubscriptionExpiringAlarms } from '../alarms';
import {
  temporaryPopupListenerForBuySubscription,
  temporaryPopupListenerForGoToFree,
} from './utils';
import { openExtensionPopup } from '@background/utils/openExtensionPopup';

export async function setupSubscriptionExpiringToastMessageHandlerListener(
  message: unknown,
): Promise<void> {
  if (isMessageOfType(message, Actions.SubscriptionExpiringToastClicked)) {
    const status = message.payload.status;
    try {
      if (status !== 'close') {
        try {
          if (status === 'buy-subscription') {
            if (
              browser_api.runtime.onMessage.hasListener(
                temporaryPopupListenerForBuySubscription,
              )
            ) {
              browser_api.runtime.onMessage.removeListener(
                temporaryPopupListenerForBuySubscription,
              );
            }

            browser_api.runtime.onMessage.addListener(
              temporaryPopupListenerForBuySubscription,
            );
          } else {
            if (
              browser_api.runtime.onMessage.hasListener(
                temporaryPopupListenerForGoToFree,
              )
            ) {
              browser_api.runtime.onMessage.removeListener(
                temporaryPopupListenerForGoToFree,
              );
            }

            browser_api.runtime.onMessage.addListener(
              temporaryPopupListenerForGoToFree,
            );
          }
        } catch (error) {
          console.error('Error navigating for subscription expiring:', error);
        } finally {
          await openExtensionPopup();
        }
      }
    } catch (error) {
      console.error(
        'Error handling subscription expiring toast clicked:',
        error,
      );
    } finally {
      await initSubscriptionExpiringAlarms();
    }
  }
}
