import { browser_api } from '@app/browser_api';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import {
  checkShouldSkipCaptcha,
  redirectToVideo,
} from '@background/redirect/utils';
import { logger } from '@utils/logger';

export async function webRequestOnBeforeRedirectListener(
  details: browser_api.WebRequest.OnBeforeRedirectDetailsType,
) {
  logger.info('youboost:webRequestOnBeforeRedirectListener', details);
  const isCaptchaUrl =
    details.redirectUrl.includes('google.com/sorry') &&
    details.url.includes('youtube.com/watch');

  const shouldSkipCaptcha = await checkShouldSkipCaptcha();

  if (!shouldSkipCaptcha && isCaptchaUrl) {
    const { currentState } = await getLocalStorageData(
      LocalStorageKey.CurrentState,
    );
    if (currentState === 'connected') {
      await redirectToVideo(details.url, details.tabId);
    }
  }
}
