import { Feature } from '@app/features/types/Feature';
import { Actions } from '../../app/actions/shared';
import {
  type DirectVideoOpenMessage,
  isMessageOfType,
} from '../../app/messages/shared';
import { analytics } from '../analytics/ga';
import { getLocalStorageData } from '@app/storage/local/shared';
import { featureStore } from '@app/features/list';
import { browser_api } from '@app/browser_api';
import type { Runtime } from 'webextension-polyfill';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { backgroundScope } from '@background/network/sentryScopeBackground';

export function getWatchUrl(url: string): string {
  const urlObj = new URL(url);

  const videoId = urlObj.searchParams.get('v');

  if (!videoId) {
    return url;
  }

  const list = urlObj.searchParams.get('list');
  const index = urlObj.searchParams.get('index');

  const params = new URLSearchParams();

  if (videoId) {
    params.set('v', videoId);
  }

  if (list) {
    params.set('list', list);
  }

  if (index) {
    params.set('index', index);
  }

  return `/watch?${params.toString()}`;
}

export function getVideoId(url: string): string | null {
  const urlObj = new URL(url);
  return urlObj.searchParams.get('v');
}

export async function getVideoTime(url: string): Promise<string | null> {
  const { savedTime } = await getLocalStorageData(LocalStorageKey.SavedTime);
  const urlObj = new URL(url);
  const videoId = getVideoId(url);

  if (videoId && savedTime?.[videoId]) {
    return savedTime[videoId].toString();
  }
  return urlObj.searchParams.get('t');
}

export async function redirectToVideo(
  url: string,
  tabId: number,
): Promise<void> {
  try {
    const videoId = getVideoId(url);

    if (videoId) {
      const watchUrl = getWatchUrl(url);

      const tabs = await browser_api.tabs.query({
        active: true,
        currentWindow: true,
      });

      const activeTab = tabs.length > 0 ? tabs[0] : undefined;

      if (activeTab?.id === tabId) {
        await analytics.trackUserAction('video_redirect', 'youtube', videoId);

        browser_api.runtime.onMessage.addListener(async function listener(
          message,
          { tab },
        ) {
          if (
            isMessageOfType(message, Actions.VideoOpenerReady) &&
            tab?.id === tabId
          ) {
            const time = await getVideoTime(url);
            await browser_api.tabs.sendMessage<DirectVideoOpenMessage>(tabId, {
              action: Actions.DirectVideoOpen,
              payload: {
                watchUrl,
                videoId,
                time,
              },
            });
            browser_api.runtime.onMessage.removeListener(listener);
          }
        } satisfies Runtime.OnMessageListenerAsync);

        backgroundScope.setTag('event_type', 'tabs_update');
        backgroundScope.setTag('tab_utl', activeTab?.url || '');
        backgroundScope.setTag('component', 'redirectToVideo');
        backgroundScope.captureMessage('Updating tab');

        await browser_api.tabs.update(tabId, {
          url: `https://www.youtube.com`,
        });

        return;
      }
    }
  } catch (error) {
    console.error('[REDIRECT TO VIDEO] Error', error);
  }
}

export async function checkShouldSkipCaptcha() {
  const { featuresList } = await featureStore.getSnapshotAsync();

  const isFeatureEnabled = !!featuresList?.[Feature.CaptchaCrackerDisabled];

  return isFeatureEnabled;
}
