import {
  type LocalStorageData,
  type LocalStorageKeyType,
  type LocalStorageValue,
} from '@app/storage/local/types/sharedTypes';
import { appStore } from 'src/store/appStore';
import { logger } from '@utils/logger';

// TODO FRO-501      appStore.setState.
export function storageOnChangedListenerStoreTemp<
  K extends LocalStorageKeyType,
>(changes: unknown) {
  const data = changes as Record<
    K,
    { newValue: LocalStorageValue<K>; oldValue: LocalStorageValue<K> }
  >;
  const newState: Partial<LocalStorageData> = {};

  for (const key in data) {
    newState[key as K] = data[key].newValue;
  }

  appStore.setState((prev) => ({
    ...prev,
    ...newState,
  }));
}
