import { logger } from '@utils/logger';
import { revokeBlobUrl } from './blobUrlManager';

export function createBrowserProxyConfig(
  pacScript: string,
  browser: 'chrome' | 'firefox',
) {
  if (browser === 'chrome') {
    return { mode: 'pac_script', pacScript: { data: pacScript } };
  }

  const FIREFOX_PAC_STRING_LIMIT_MB = process.env.FIREFOX_PAC_STRING_LIMIT_MB
    ? parseInt(process.env.FIREFOX_PAC_STRING_LIMIT_MB)
    : 1;
  const scriptSize = new TextEncoder().encode(pacScript).length;
  const sizeInMB = scriptSize / (1024 * 1024);

  let pacUrl = '';

  if (sizeInMB > FIREFOX_PAC_STRING_LIMIT_MB) {
    const blob = new Blob([pacScript], {
      type: 'application/x-ns-proxy-autoconfig',
    });
    pacUrl = URL.createObjectURL(blob);

    revokeBlobUrl(pacUrl);

    logger.info(
      `PAC script is bigger than ${FIREFOX_PAC_STRING_LIMIT_MB} and is `,
      sizeInMB,
      'Mb',
    );
  } else {
    revokeBlobUrl();

    pacUrl = 'data:application/x-ns-proxy-autoconfig;base64,' + btoa(pacScript);
  }

  return { proxyType: 'autoConfig', autoConfigUrl: pacUrl };
}
