import { AuthDetails, ChromeListener } from '@background/network/proxy/types';
import { handleProxyAuth } from '@background/network/proxy/proxyAuthHandler/handleProxyAuth';
import { logger } from '@utils/logger';

export const proxyAuthListenerChrome: ChromeListener = async (
  details,
  callback,
) => {
  try {
    if (!callback) {
      throw new Error('[proxyAuthListenerChrome] No callback provided');
    }

    const authDetails: AuthDetails = {
      challenger: details.challenger,
      isProxy: details.isProxy,
    };

    const response = await handleProxyAuth(authDetails);
    callback(response);
  } catch (e) {
    logger.error(e);
  }
};
