import { browser_api } from '@app/browser_api';
import { Feature, isFeatureStore } from '@app/features/types/Feature';
import { getPACData } from '@app/storage/local/getPACData';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function onFeatureStoreChanged(
  changes: Record<string, browser_api.Storage.StorageChange>,
) {
  const featureStoreChange = changes[LocalStorageKey.FeatureStore];

  if (
    !featureStoreChange ||
    !isFeatureStore(featureStoreChange.newValue) ||
    !isFeatureStore(featureStoreChange.oldValue)
  )
    return;

  const newFeatures = featureStoreChange.newValue.featuresList;
  const oldFeatures = featureStoreChange.oldValue.featuresList;

  if (newFeatures[Feature.PACLite] !== oldFeatures[Feature.PACLite]) {
    await getPACData({
      shouldRefetch: true,
    });
  }
}
