import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import {
  PACData,
  PACPremiumData,
} from '@app/storage/local/types/persistentTypes';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import {
  BaseProxyDataResponse,
  PremiumProxies,
  PremiumProxyData,
  PremiumProxyDataResponse,
  ProxyConfig,
  ProxyDataResponse,
} from '../../types/ProxyData';

type ExtractPremiumProxiesResult = {
  default: PremiumProxyData;
  vpn: PremiumProxyData;
};

function isPACData(data: PACData | PACPremiumData): data is PACData {
  return (data as PACData).free !== undefined;
}

function isPACPremiumData(
  data: PACData | PACPremiumData,
): data is PACPremiumData {
  if (process.env.EXTENSION_CODE === 'vrubel') {
    return (data as PACPremiumData).premium !== undefined;
  }
  return (
    (data as PACPremiumData).premium !== undefined &&
    (data as PACPremiumData).allInternet !== undefined
  );
}

function extractPremiumProxies(
  proxies: PremiumProxies,
): [PremiumProxyData, PremiumProxyData] {
  const result = proxies.reduce<ExtractPremiumProxiesResult>((acc, proxy) => {
    if (proxy.proxy_type === 'default') {
      acc.default = proxy;
    } else if (proxy.proxy_type === 'vpn') {
      acc.vpn = proxy;
    }
    return acc;
  }, {} as ExtractPremiumProxiesResult);

  return [result.default, result.vpn];
}

function processPremiumPACData(
  pacData: any,
  proxyData: PremiumProxyDataResponse,
  isAllInternetProxy?: boolean,
): ProxyConfig {
  const { proxies } = proxyData;
  const [premiumProxy, allInternetProxy] = extractPremiumProxies(proxies);

  let pacString = pacData.premium;
  let vpnProxyHost: string | undefined;
  let vpnProxyPort: number | undefined;

  if (isAllInternetProxy) {
    pacString = pacData.allInternet;
    vpnProxyHost = allInternetProxy.host;
    vpnProxyPort = allInternetProxy.port;
  }

  return {
    pacString,
    proxyHost: premiumProxy.host,
    proxyPort: premiumProxy.port,
    vpnProxyHost,
    vpnProxyPort,
    shouldDecompress: true,
  };
}

function processFreePACData(
  pacData: any,
  proxyData: BaseProxyDataResponse,
): ProxyConfig {
  if (!proxyData.data.proxy_server) {
    throw new Error('Proxy data is missing');
  }

  return {
    pacString: pacData.free,
    proxyHost: proxyData.data.proxy_server.host,
    proxyPort: proxyData.data.proxy_server.port,
    shouldDecompress: false,
  };
}

export async function processPACData(
  pacData: any,
  proxyData: ProxyDataResponse,
): Promise<ProxyConfig> {
  const { userId, hasPremium, isAllInternetProxy } = await getLocalStorageData([
    LocalStorageKey.UserId,
    LocalStorageKey.HasPremium,
    LocalStorageKey.IsAllInternetProxy,
  ]);

  if (isPACPremiumData(pacData) && userId && hasPremium) {
    return processPremiumPACData(
      pacData,
      proxyData as PremiumProxyDataResponse,
      isAllInternetProxy,
    );
  } else if (isPACData(pacData)) {
    return processFreePACData(pacData, proxyData as BaseProxyDataResponse);
  } else {
    await setLocalStorageData({ currentState: 'error' });
    throw new Error('Invalid PAC data format');
  }
}
