import { Alarms } from '@app/alarms/shared';
import { browser_api } from '@app/browser_api';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export function onIsConnectionButtonDisabledChanged(
  changes: Record<string, browser_api.Storage.StorageChange>,
) {
  const delayInMinutes = process.env.ENABLE_CONNECTION_BUTTON_DELAY_MINUTES
    ? parseInt(process.env.ENABLE_CONNECTION_BUTTON_DELAY_MINUTES)
    : 1;

  const isConnectionButtonDisabled =
    changes[LocalStorageKey.IsConnectionButtonDisabled].newValue;

  if (isConnectionButtonDisabled) {
    browser_api.alarms.create(Alarms.EnableConnectionButton, {
      delayInMinutes,
    });
  }
}
