import { getSubscriptionInfo } from '@utils/getSubscriptionInfo';
import { logger } from '@utils/logger';
import { checkAlarmTries } from '../utils/checkAlarmTries';
import { PremiumAlarms } from '@app/alarms/premium';
import { browser_api } from '@app/browser_api';

export async function paymentStarted() {
  logger.info('youboost:Payment started');
  const info = await getSubscriptionInfo();
  // We need to check if alarm tries are not exceeded
  await checkAlarmTries(PremiumAlarms.PaymentStarted);

  // We need to check if the payment is completed
  if (info?.data?.subscription?.status === 'Paid') {
    await browser_api.alarms.clear(PremiumAlarms.PaymentStarted);
  }
}
