import { Actions } from "@app/actions/shared";
import { browser_api } from "@app/browser_api";
import { isMessageOfType, ShowChangeServerScreenMessage} from "@app/messages/shared";

export async function temporaryPopupListenerForChangeServer(
    message: unknown,
  ): Promise<void> {
    if (isMessageOfType(message, Actions.PopupReady)) {
      await browser_api.runtime.sendMessage<ShowChangeServerScreenMessage>(
        {
          action: Actions.ShowChangeServerScreen,
        },
      );
  
      browser_api.runtime.onMessage.removeListener(
        temporaryPopupListenerForChangeServer,
      );
    }
  }