import { browser_api } from '@app/browser_api';
import { getSupportedLanguages } from '@app/i18n/utils';
import { featureStore } from '@app/features/list';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { createOrUpdatePremiumProxyDataAlarm } from '@background/alarmHandlers/createOrUpdatePremiumProxyDataAlarm';
import { updatePacScript } from '@background/pac-script/updatePacScript';
import { moveUserIdAndDeviceIdFromCookiesToLocalStorage } from '@background/user/moveUserIdFromCookiesToLocalStorage';
import { openExtensionPopup } from '@background/utils/openExtensionPopup';
import { detectBrowserLanguage } from '@background/utils/detectBrowserLanguage';
import { getConfigsUrl } from '@utils/getConfigsUrl';
import { logger } from '@utils/logger';
import { getMetaApiData } from '@utils/metaApiData';
import { BrowserInfo, detect } from 'detect-browser';

export async function runtimeOnInstalledListener(
  details: browser_api.Runtime.OnInstalledDetailsType,
) {
  logger.info('shared:runtimeOnInstalledListener');
  try {
    await getMetaApiData();

    const isDevModeFromEnv = process.env.DEV_MODE === 'true';

    const { isDevMode } = await getLocalStorageData(LocalStorageKey.IsDevMode);

    if (typeof isDevMode !== 'boolean') {
      await setLocalStorageData({
        [LocalStorageKey.IsDevMode]: isDevModeFromEnv,
      });
    }

    featureStore.init();

    if (details.reason === 'install') {
      logger.info('install');
      try {
        const { supportedBrowsers } = await getMetaApiData();

        const extensionCode = process.env.EXTENSION_CODE;
        const welcomeUrl = await getConfigsUrl(process.env.WELCOME_URL);

        if (
          welcomeUrl &&
          extensionCode !== 'youboost-neo' &&
          extensionCode !== 'youboost'
        ) {
          logger.info('create welcome url', welcomeUrl.toString());
          await browser_api.tabs.create({ url: welcomeUrl.toString() });
        }

        const browser = detect();
        logger.info('browser', browser);

        const isBrowserSupported =
          browser instanceof BrowserInfo &&
          supportedBrowsers.includes(browser.name);

        logger.info('isBrowserSupported', isBrowserSupported);

        if (!isBrowserSupported) {
          logger.info('set isBrowserError to true');
          await setLocalStorageData({ isBrowserError: true });
          await openExtensionPopup();
        }

        await updatePacScript();
        await moveUserIdAndDeviceIdFromCookiesToLocalStorage();

        //       
        const { currentLocale: existingLang } = await getLocalStorageData(
          LocalStorageKey.CurrentLocale,
        );
        if (!existingLang) {
          const supportedLngs = await getSupportedLanguages();
          const detectedLang = detectBrowserLanguage(supportedLngs);
          await setLocalStorageData({
            [LocalStorageKey.CurrentLocale]: detectedLang,
          });
          logger.info('Detected browser language', detectedLang);
        }
      } catch (err) {
        console.error(err);
        await setLocalStorageData({ [LocalStorageKey.CurrentState]: 'error' });
      }
    }

    if (details.reason === 'update') {
      logger.info('update');
      await updatePacScript();
    }

    await createOrUpdatePremiumProxyDataAlarm();
  } catch (error) {
    logger.error('runtimeOnInstalledListener error', error);
  }
}
