import { browser_api } from '@app/browser_api';
import { logger } from '@utils/logger';

export async function openExtensionPopup(): Promise<void> {
  logger.info('youboost:openExtensionPopup');
  try {
    if ('openPopup' in browser_api.action) {
      const window = await browser_api.windows.getCurrent();
      if (window.id) {
        await browser_api.windows.update(window.id, { focused: true });
      }
      await browser_api.action.openPopup({ windowId: window.id });
    }
  } catch (err) {
    console.error('Error opening extension popup:', err);
  }
}
