import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { createOrUpdatePremiumProxyDataAlarm } from '@background/alarmHandlers/createOrUpdatePremiumProxyDataAlarm';
import { updatePremiumProxyData } from '@background/network/proxy/utils';
import { logger } from '@utils/logger';

export async function onHasPremiumChanged() {
  try {
    logger.info('shared:onHasPremiumChanged');

    await createOrUpdatePremiumProxyDataAlarm();

    const { currentState } = await getLocalStorageData(
      LocalStorageKey.CurrentState,
    );

    if (currentState === 'connected') {
      await updatePremiumProxyData();
    }
  } catch (error) {
    console.error('onHasPremiumChanged error', error);
  }
}
