/**
 * Returns a random number in minutes within the range from min to max inclusive
 * @param min - minimum time in minutes
 * @param max - maximum time in minutes
 * @returns random number in minutes
 */
export function getRandomTimeInterval(min: number, max: number): number {
  if (min > max) {
    throw new Error('Minimum value cannot be greater than maximum value');
  }

  return Math.floor(Math.random() * (max - min + 1)) + min;
}
