import { getUserCountry } from './getUserCountry';
import { type SupportedLanguages } from '@app/i18n/types';
import { PRIORITY_LANGUAGE } from '../constants';
import { logger } from '@utils/logger';

export type SetLocaleParams = {
  lastSettedLocale: string | null;
  currentLanguage: SupportedLanguages;
  changeLanguage: (language: SupportedLanguages) => void;
};

export const setLocale = async ({
  lastSettedLocale,
  currentLanguage,
  changeLanguage,
}: SetLocaleParams) => {
  if (!lastSettedLocale && currentLanguage !== PRIORITY_LANGUAGE) {
    const userCountry = await getUserCountry();
    logger.info('Received user country:', userCountry);

    if (userCountry === PRIORITY_LANGUAGE) {
      logger.info(
        'User country is equal to priority language, set priority language',
      );
      changeLanguage(PRIORITY_LANGUAGE);
    }
  } else {
    logger.info(
      'Locale is already set or system language is equal to priority language',
    );
  }
};
