import { browser_api } from '@app/browser_api';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { logger } from '@utils/logger';

export async function syncLanguageToStorage(language: string): Promise<void> {
  try {
    await browser_api.storage.local.set({
      [LocalStorageKey.CurrentLocale]: language,
    });
  } catch (error) {
    logger.error('Failed to sync language to storage:', error);
  }
}
