import { browser_api } from '@app/browser_api';
import { detectBrowser } from '@app/utils/detectBrowser';
import { GetPacScriptDto } from '../types/proxyInBrowserTypes';
import {
  createBrowserProxyConfig,
  getPacScript,
  handleProxySetError,
  handleProxySetSuccess,
} from './utils';

export async function setProxy({
  host,
  port,
  pacString,
  shouldDecompress,
  vpnHost,
  vpnPort,
}: GetPacScriptDto): Promise<void> {
  const pacScript = getPacScript({
    host,
    port,
    pacString,
    shouldDecompress,
    vpnHost,
    vpnPort,
  });

  const browser = detectBrowser();
  const browserProxyConfig = createBrowserProxyConfig(pacScript, browser);

  const data = browserProxyConfig.pacScript?.data.includes('ytimg.com');

  console.warn('ytimg.com in pac data:', data);

  await browser_api.proxy.settings.set({
    value: browserProxyConfig,
    scope: 'regular',
  });

  if (browser_api.runtime.lastError) {
    handleProxySetError(
      'Error setting proxy:',
      browser_api.runtime.lastError.message,
    );
  } else {
    await handleProxySetSuccess(host, port);
  }
}
