import { browser_api } from '@app/browser_api';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { onOnboardingScreenStatusChanged } from '@background/localStorageHandlers/onOnboardingScreenStatusChanged';
import { backgroundScope } from '@background/network/sentryScopeBackground';
import { logger } from '@utils/logger';
import { getMetaApiData } from '@utils/metaApiData';
import { analytics } from '../analytics/ga';
import { initProxy } from '../network/proxy/proxyControl';
import { onAlarmListener } from './youboost/listeners/onAlarmListener';
import { runtimeOnInstalledListener } from './youboost/listeners/runtimeOnInstalledListener';
import { runtimeOnMessageListener } from './youboost/listeners/runtimeOnMessageListener';
import { storageLocalOnChangedListener } from './youboost/listeners/storageLocalOnChangedListener';
import { webRequestOnBeforeRedirectListener } from './youboost/listeners/webRequestOnBeforeRedirectListener';
import '../shared';
import { setSentryTags } from '@utils/setSentryTags';

if (browser_api.runtime.onInstalled.hasListener(runtimeOnInstalledListener)) {
  browser_api.runtime.onInstalled.removeListener(runtimeOnInstalledListener);
  logger.info('youboost:remove runtimeOnInstalledListener');
}

browser_api.runtime.onInstalled.addListener(runtimeOnInstalledListener);
logger.info('youboost:add runtimeOnInstalledListener');

if (browser_api.runtime.onMessage.hasListener(runtimeOnMessageListener)) {
  browser_api.runtime.onMessage.removeListener(runtimeOnMessageListener);
  logger.info('youboost:remove runtimeOnMessageListener');
}

browser_api.runtime.onMessage.addListener(runtimeOnMessageListener);
logger.info('youboost:add runtimeOnMessageListener');

if (browser_api.alarms.onAlarm.hasListener(onAlarmListener)) {
  browser_api.alarms.onAlarm.removeListener(onAlarmListener);
  logger.info('youboost:remove onAlarmListener');
}

browser_api.alarms.onAlarm.addListener(onAlarmListener);
logger.info('youboost:add onAlarmListener');

if (
  browser_api.storage.local.onChanged.hasListener(storageLocalOnChangedListener)
) {
  browser_api.storage.local.onChanged.removeListener(
    storageLocalOnChangedListener,
  );
  logger.info('youboost:remove storageLocalOnChangedListener');
}

browser_api.storage.local.onChanged.addListener(storageLocalOnChangedListener);
logger.info('youboost:add storageLocalOnChangedListener');

if (
  browser_api.webRequest.onBeforeRedirect.hasListener(
    webRequestOnBeforeRedirectListener,
  )
) {
  browser_api.webRequest.onBeforeRedirect.removeListener(
    webRequestOnBeforeRedirectListener,
  );
  logger.info('youboost:remove webRequestOnBeforeRedirectListener');
}

browser_api.webRequest.onBeforeRedirect.addListener(
  webRequestOnBeforeRedirectListener,
  { urls: ['*://*.youtube.com/*'], types: ['main_frame'] },
  [],
);

logger.info('youboost:add webRequestOnBeforeRedirectListener');

async function init(): Promise<void> {
  logger.info('youboost:init');
  try {
    const { isBrowserError } = await getLocalStorageData(
      LocalStorageKey.IsBrowserError,
    );

    await setSentryTags();

    if (isBrowserError) {
      logger.info('youboost:isBrowserError');
      await analytics.trackError('Browser error detected', 'initialization');
      void setLocalStorageData({
        [LocalStorageKey.CurrentState]: 'disconnected',
        [LocalStorageKey.TargetState]: 'disconnected',
      });
    } else {
      logger.info('youboost:not isBrowserError');
      await getMetaApiData();

      const shouldAutostart = process.env.SHOULD_AUTOSTART === 'true';

      if (shouldAutostart) {
        logger.info('youboost:shouldAutostart');
        await analytics.trackUserAction('autostart', 'proxy');
        await initProxy();
      } else {
        logger.info('youboost:not shouldAutostart');
        await analytics.trackUserAction('manual_start', 'proxy');
      }

      await setLocalStorageData({ [LocalStorageKey.IsInitialized]: true });
      await onOnboardingScreenStatusChanged();
    }
  } catch (error) {
    logger.error('youboost:error', error);

    backgroundScope.setTag('error_type', 'initialization_failure');
    backgroundScope.setTag('component', 'background_init');
    backgroundScope.captureException(error);

    await analytics.trackError(
      error instanceof Error ? error.message : 'Unknown error',
      'initialization',
    );
    await setLocalStorageData({ [LocalStorageKey.CurrentState]: 'error' });
  }
}

await init();
