import { PremiumAlarms } from '@app/alarms/premium';
import { browser_api } from '@app/browser_api';
import { setLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { logger } from '@utils/logger';

export async function hideAdPremiumBanner() {
  logger.info('youboost:Hide ad premium banner');
  await setLocalStorageData({ [LocalStorageKey.IsAdBannerShown]: false });
  await browser_api.alarms.clear(PremiumAlarms.HideAdPremiumBanner);
}
