import { updatePacScript } from '@background/pac-script/updatePacScript';
import { logger } from '@utils/logger';

export async function updatePacDataAlarm(): Promise<void> {
  try {
    logger.info('shared:updatePacDataAlarm');
    await updatePacScript();
  } catch (error) {
    console.error('shared:updatePacDataAlarm error', error);
  }
}
