import { browser_api } from '@app/browser_api';
import { PRIORITY_LANGUAGE } from '@app/i18n/constants';
import { SupportedLanguages } from '@app/i18n/types';

export function detectBrowserLanguage(
  supportedLanguages: readonly string[],
): SupportedLanguages {
  const browserLang = browser_api.i18n.getUILanguage();

  if (supportedLanguages.includes(browserLang)) {
    return browserLang as SupportedLanguages;
  }

  const browserLangPrefix = browserLang.split('-')[0].toLowerCase();
  const matchedLang = supportedLanguages.find(
    (lang) => lang.toLowerCase() === browserLangPrefix,
  );

  if (matchedLang) {
    return matchedLang as SupportedLanguages;
  }

  return PRIORITY_LANGUAGE;
}
