import { deactivateDevice } from '@api/premium/deactivateDevice';
import { UserStatus } from '@api/premium/types/UserStatus';
import { getDeactivateDeviceApiUrl } from '@api/urls';
import { PremiumActions } from '@app/actions/premium';
import { Actions } from '@app/actions/shared';
import { PremiumAlarms } from '@app/alarms/premium';
import { browser_api } from '@app/browser_api';
import { isPremiumMessageOfType } from '@app/messages/premium';
import {
  isMessageOfType,
  NavigateToFreeTrialMessage,
  NavigateToPaymentMessage,
  ChangeServerManuallyCompletedMessage,
} from '@app/messages/shared';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { createAndInitializeUserWithStatus } from '@background/client/premium';
import { openExtensionPopup } from '@background/utils/openExtensionPopup';
import { logger } from '@utils/logger';
import { getMetaApiData } from '@utils/metaApiData';
import { changeServerManually } from '@background/network/proxy/proxyControl/changeServerManually';

export async function runtimeOnMessageListener(message: unknown) {
  logger.info('youboost:runtimeOnMessageListener', message);
  if (isMessageOfType(message, Actions.DeactivateDevice)) {
    try {
      logger.info('Deactivate device');
      const { apiBaseUrl } = await getMetaApiData();
      const response = await deactivateDevice({
        url: getDeactivateDeviceApiUrl(apiBaseUrl),
        body: message.payload,
      });
      if (response.data.success !== true || !!response.error) {
        throw new Error('Error on deactivate device');
      }
    } catch (error) {
      console.error(error);
    }
  }

  if (isMessageOfType(message, Actions.OpenPopupRoot)) {
    logger.info('Open popup root');
    await openExtensionPopup();
  }

  if (isMessageOfType(message, Actions.OpenPopup)) {
    logger.info('Open popup');
    await openExtensionPopup();

    browser_api.runtime.onMessage.addListener(async function listener(
      msg: unknown,
    ) {
      try {
        const { userId } = await getLocalStorageData(LocalStorageKey.UserId);

        if (isMessageOfType(msg, Actions.PopupReady) && userId) {
          await browser_api.runtime.sendMessage<NavigateToFreeTrialMessage>({
            action: Actions.NavigateToFreeTrial,
          });
          browser_api.runtime.onMessage.removeListener(listener);
        }
      } catch (error) {
        console.error(error);
      }
    });
  }

  if (isMessageOfType(message, Actions.OpenPopupChangeDevice)) {
    logger.info('Open popup change device');
    // TODO we need to find solution for open popup on change device
  }

  if (isMessageOfType(message, Actions.CreateNewUser)) {
    logger.info('Create new user');
    await createAndInitializeUserWithStatus(UserStatus.New, message.payload);
  }

  if (isMessageOfType(message, Actions.OpenPopupPayment)) {
    logger.info('Open popup payment');
    await openExtensionPopup();

    browser_api.runtime.onMessage.addListener(async function listener(
      msg: unknown,
    ) {
      if (isMessageOfType(msg, Actions.PopupReady)) {
        await browser_api.runtime.sendMessage<NavigateToPaymentMessage>({
          action: Actions.NavigateToPayment,
        });
        browser_api.runtime.onMessage.removeListener(listener);
      }
    });
  }

  if (isPremiumMessageOfType(message, PremiumActions.InitAdPremiumBanner)) {
    logger.info('Init ad premium banner');
    const { hasPremium } = await getLocalStorageData([
      LocalStorageKey.HasPremium,
    ]);

    const isAlarmEnabled = await browser_api.alarms.get(
      PremiumAlarms.ShowAdPremiumBanner,
    );

    if (isAlarmEnabled && hasPremium) {
      await browser_api.alarms.clear(PremiumAlarms.ShowAdPremiumBanner);
      return;
    }

    if (!hasPremium && !isAlarmEnabled) {
      browser_api.alarms.create(PremiumAlarms.ShowAdPremiumBanner, {
        delayInMinutes: 0,
        periodInMinutes: 24 * 60,
      });
    }
  }

  if (isPremiumMessageOfType(message, PremiumActions.CloseAdPremiumBanner)) {
    logger.info('Close ad premium banner');
    browser_api.alarms.create(PremiumAlarms.HideAdPremiumBanner, {
      delayInMinutes: 0,
    });
  }

  // This alarm will be stopped when the payment is completed
  if (isPremiumMessageOfType(message, PremiumActions.PaymentStarted)) {
    logger.info('Payment started');
    const { showAdPremiumBannerPeriodInMinutes = 24 * 60 } =
      await getMetaApiData();

    browser_api.alarms.create(PremiumAlarms.PaymentStarted, {
      periodInMinutes: showAdPremiumBannerPeriodInMinutes,
    });
  }

  if (isPremiumMessageOfType(message, PremiumActions.PremiumCanceled)) {
    logger.info('Premium canceled');
    browser_api.alarms.create(PremiumAlarms.PremiumCanceled, {
      periodInMinutes: 0.1,
    });
  }

  if (isMessageOfType(message, Actions.ChangeServerManually)) {
    logger.info('Change server manually request');
    try {
      const result = await changeServerManually();
      await browser_api.runtime.sendMessage<ChangeServerManuallyCompletedMessage>({
        action: Actions.ChangeServerManuallyCompleted,
        payload: result,
      });
    } catch (e) {
      await browser_api.runtime.sendMessage<ChangeServerManuallyCompletedMessage>({
        action: Actions.ChangeServerManuallyCompleted,
        payload: {
          status: 'error',
          message: e instanceof Error ? e.message : 'Unknown error',
        },
      });
    }
  }
}
