import { getPacMetaType, PacMetaType } from '@api/getPacMeta/types';
import { getConfigsUrl } from '@utils/getConfigsUrl';
import { configsClient } from '@api/configsClient';

export const getPacMeta: getPacMetaType = async (
  endpoint = process.env.PAC_META_API_URL,
) => {
  try {
    const pacMetaApiUrl = await getConfigsUrl(endpoint);

    if (!pacMetaApiUrl) {
      throw new Error('No pac meta API URL provided');
    }

    return await configsClient
      .get<PacMetaType>(pacMetaApiUrl.toString())
      .json();
  } catch (error) {
    console.error(error);
    throw error;
  }
};
