import { setProxy } from "../../proxyInBrowser";
import { ProxyConfig } from "../../types/ProxyData";

export async function configureProxy(proxyConfig: ProxyConfig): Promise<void> {
    await setProxy({
      port: proxyConfig.proxyPort,
      host: proxyConfig.proxyHost,
      vpnHost: proxyConfig.vpnProxyHost,
      vpnPort: proxyConfig.vpnProxyPort,
      pacString: proxyConfig.pacString,
      shouldDecompress: proxyConfig.shouldDecompress,
    });
  }