import { Actions } from '@app/actions/shared';
import { Alarms, getChromeAlarm } from '@app/alarms/shared';
import { isMessageOfType } from '@app/messages/shared';
import { browser_api } from '@app/browser_api';
import {
  deleteLocalStorageData,
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { acceptGift } from '@api/gifts';
import { getMetaApiData } from '@utils/metaApiData';
import { getAcceptGiftApiUrl } from '@api/urls';
import { featureStore } from '@app/features/list';
import { Feature } from '@app/features/types/Feature';
import { getSubscriptionInfo } from '@utils/getSubscriptionInfo';
import { Runtime } from 'webextension-polyfill';
import {
  GiftMessageHandlerOptions,
  PopupListenerOptions,
  PopupScreenMessage,
} from '@background/gifts/types';
import dayjs from 'dayjs';
import { logger } from '@utils/logger';
import { openExtensionPopup } from '@background/utils/openExtensionPopup';

const handleRefuseGift = async () => {
  try {
    const { giftAdvertisingAvailableDates } = await getMetaApiData();
    const endDateString = giftAdvertisingAvailableDates?.[1];
    if (!endDateString) {
      throw new Error('giftAdvertisingAvailableDates is not defined');
    }

    const endDate = dayjs(endDateString);
    const now = dayjs();
    if (now.isAfter(endDate)) {
      logger.warn('[GIFT] Campaign already ended, nothing to do');
      return;
    }

    logger.info('[GIFT] endDate', endDate);
    await setLocalStorageData({
      [LocalStorageKey.GiftRefusedUntil]: endDate.toISOString(),
    });
    await browser_api.alarms.clear(Alarms.ShowGiftOnYoutube);
    await sendMessageToYoutubeTabs(Actions.HideGift);
    logger.warn(`[GIFT] Gift feature muted until ${endDate.toISOString()}`);
  } catch (e) {
    console.error('[GIFT] Error handling refuse gift', e);
  }
};

const sendMessageToYoutubeTabs = async (
  action: Actions.HideGift | Actions.InitGiftAdvertisingScript,
) => {
  try {
    const youtubeTabs = await browser_api.tabs.query({
      url: ['*://*.youtube.com/*'],
    });
    await Promise.all(
      youtubeTabs
        .filter((tab) => tab.id)
        .map((tab) => browser_api.tabs.sendMessage(tab.id!, { action })),
    );
  } catch (error) {
    console.error('[GIFT] Error sending message to youtube tabs', error);
  }
};

function isGiftAdvertisingAvailable(
  dates: [string, string] | undefined,
): boolean {
  if (!dates || dates.length !== 2 || !dates[0] || !dates[1]) {
    return false;
  }

  const [startDateString, endDateString] = dates;
  const startDate = new Date(startDateString);
  const endDate = new Date(endDateString);
  const currentDate = new Date();
  return currentDate >= startDate && currentDate <= endDate;
}

async function giftAlarmHandlerListener(
  alarm: browser_api.Alarms.Alarm,
): Promise<void> {
  const name = alarm.name as Alarms;

  const { currentState } = await getLocalStorageData(
    LocalStorageKey.CurrentState,
  );

  if (name === Alarms.ShowGiftOnYoutube && currentState === 'connected') {
    try {
      await sendMessageToYoutubeTabs(Actions.InitGiftAdvertisingScript);
    } catch (error) {
      console.error('Error handling gift alarm:', error);
    }
  }
}

export function addPopupScreenListener({
  imageUrl,
  action,
}: PopupListenerOptions) {
  const listener: Runtime.OnMessageListener = async (message: unknown) => {
    if (isMessageOfType(message, Actions.PopupReady)) {
      await browser_api.runtime.sendMessage<PopupScreenMessage>({
        action,
        payload: { imageUrl },
      });

      browser_api.runtime.onMessage.removeListener(listener);
    }
  };

  browser_api.runtime.onMessage.addListener(listener);
}

async function handleGiftMessage({
  message,
  popupAction,
}: GiftMessageHandlerOptions): Promise<void> {
  try {
    const { imageUrl } = (message as any)?.payload ?? {};
    if (!imageUrl) {
      throw new Error('Missing imageUrl in gift message payload.');
    }

    await browser_api.alarms.clear(Alarms.ShowGiftOnYoutube);

    const { giftAdvertisingPeriodInMinutes, apiBaseUrl } =
      await getMetaApiData();

    const periodInMinutes = giftAdvertisingPeriodInMinutes
      ? Number(giftAdvertisingPeriodInMinutes)
      : null;

    if (periodInMinutes) {
      browser_api.alarms.create(Alarms.ShowGiftOnYoutube, {
        delayInMinutes: periodInMinutes,
        periodInMinutes,
      });
    }

    const isAcceptingGift = popupAction === Actions.ShowGiftAdvertisingScreen;
    let shouldOpenRootPage = false;

    if (isAcceptingGift) {
      const { userId, deviceId } = await getLocalStorageData([
        LocalStorageKey.UserId,
        LocalStorageKey.DeviceId,
      ]);

      if (!userId) {
        throw new Error('User ID is not defined.');
      }

      if (!deviceId) {
        throw new Error('Device ID is not defined.');
      }

      try {
        await acceptGift(getAcceptGiftApiUrl(apiBaseUrl), {
          userId,
          deviceId,
        });
      } catch (error) {
        console.error('Error accepting gift:', error);
        shouldOpenRootPage = true;
      }
    }

    try {
      if (!shouldOpenRootPage) {
        await getSubscriptionInfo();
        addPopupScreenListener({ imageUrl, action: popupAction });
      }
    } catch (error) {
      console.error('Error showing popup screen:', error);
    } finally {
      await openExtensionPopup();
    }

    await sendMessageToYoutubeTabs(Actions.HideGift);
  } catch (error) {
    console.error('Error handling gift message:', error);
  }
}

async function setupGiftMessageHandlerListener(
  message: unknown,
): Promise<void> {
  if (isMessageOfType(message, Actions.GiftClicked)) {
    await handleGiftMessage({
      message,
      popupAction: Actions.ShowGiftAdvertisingScreen,
    });
  }

  if (isMessageOfType(message, Actions.CloseGift)) {
    await handleGiftMessage({
      message,
      popupAction: Actions.ShowRefuseGiftScreen,
    });
  }

  if (isMessageOfType(message, Actions.RefuseGift)) {
    await handleRefuseGift();
  }
}

export async function initGiftAlarms(): Promise<void> {
  try {
    const { giftAdvertisingPeriodInMinutes } = await getMetaApiData();

    const periodInMinutes = giftAdvertisingPeriodInMinutes
      ? Number(giftAdvertisingPeriodInMinutes)
      : null;

    const existingAlarm = await getChromeAlarm(Alarms.ShowGiftOnYoutube);

    if (existingAlarm) {
      browser_api.alarms.clear(Alarms.ShowGiftOnYoutube);
    }

    if (periodInMinutes && !existingAlarm) {
      browser_api.alarms.create(Alarms.ShowGiftOnYoutube, {
        delayInMinutes: 0,
        periodInMinutes,
      });
    }
  } catch (error) {
    console.error('Error initializing gift alarms:', error);
  }
}

function setupGiftAlarmHandler(): void {
  if (!browser_api.alarms.onAlarm.hasListener(giftAlarmHandlerListener)) {
    browser_api.alarms.onAlarm.addListener(giftAlarmHandlerListener);
  }
}

function setupGiftMessageHandler(): void {
  if (
    !browser_api.runtime.onMessage.hasListener(setupGiftMessageHandlerListener)
  ) {
    browser_api.runtime.onMessage.addListener(setupGiftMessageHandlerListener);
  }
}

async function initGiftsOnYoutubeBackgroundListener(
  message: unknown,
): Promise<void> {
  if (isMessageOfType(message, Actions.InitGiftAdvertisingScriptReady)) {
    const { featuresList } = await featureStore.getSnapshotAsync();
    const { giftAdvertisingAvailableDates } = await getMetaApiData();
    const { giftRefusedUntil } = await getLocalStorageData(
      LocalStorageKey.GiftRefusedUntil,
    );
    const now = dayjs();
    const isRefused = Boolean(
      giftRefusedUntil && dayjs(giftRefusedUntil).isAfter(now),
    );

    if (giftRefusedUntil && dayjs(giftRefusedUntil).isBefore(now)) {
      await deleteLocalStorageData(LocalStorageKey.GiftRefusedUntil);
    }

    // We need to clean up all listeners if gift advertising is disabled or expired or not started yet
    if (
      featuresList?.[Feature.GiftAdvertising] &&
      isGiftAdvertisingAvailable(giftAdvertisingAvailableDates) &&
      !isRefused
    ) {
      setupGiftAlarmHandler();
      setupGiftMessageHandler();
      initGiftAlarms();
    } else {
      if (browser_api.alarms.onAlarm.hasListener(giftAlarmHandlerListener)) {
        browser_api.alarms.onAlarm.removeListener(giftAlarmHandlerListener);
      }

      if (
        browser_api.runtime.onMessage.hasListener(
          setupGiftMessageHandlerListener,
        )
      ) {
        browser_api.runtime.onMessage.removeListener(
          setupGiftMessageHandlerListener,
        );
      }
    }
  }
}

export function initGiftsOnYoutubeBackground(): void {
  if (
    !browser_api.runtime.onMessage.hasListener(
      initGiftsOnYoutubeBackgroundListener,
    )
  ) {
    browser_api.runtime.onMessage.addListener(
      initGiftsOnYoutubeBackgroundListener,
    );
  }
}
