import { getLocalStorageData } from '@app/storage/local/shared';
import { PingPongService } from '../services/pingPongService';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { onStartupListener } from '@background/listeners/onStartupListener';
import { featureStore } from '@app/features/list';
import { Feature } from '@app/features/types/Feature';
import { getMetaApiData } from '@utils/metaApiData';

export async function initServiceWorkerPingPong() {
  try {
    console.warn('initServiceWorkerPingPong started');
    const { featuresList } = await featureStore.getSnapshotAsync();

    const isPingPongEnabled = featuresList[Feature.ServiceWorkerPingPong];

    if (!isPingPongEnabled) {
      return;
    }

    const now = Date.now();
    const [{ proxyUpdatePeriod }, { lastProxyRequestAt }] = await Promise.all([
      getMetaApiData(),
      getLocalStorageData(LocalStorageKey.LastProxyRequestAt),
    ]);

    if (!proxyUpdatePeriod || proxyUpdatePeriod <= 0) {
      throw new Error('No proxyUpdatePeriod provided');
    }

    const minutesSinceLast =
      typeof lastProxyRequestAt === 'number' && lastProxyRequestAt > 0
        ? (now - lastProxyRequestAt) / 60000
        : null;

    const pingPong = new PingPongService();
    await pingPong.init();

    const isServiceWorkerHasSlept = await pingPong.isServiceWorkerHasSlept();

    const { currentState } = await getLocalStorageData(
      LocalStorageKey.CurrentState,
    );

    const periodIsPassed =
      typeof minutesSinceLast === 'number' &&
      minutesSinceLast >= proxyUpdatePeriod;

    if (
      currentState === 'connected' &&
      isServiceWorkerHasSlept &&
      periodIsPassed
    ) {
      if (isServiceWorkerHasSlept) {
        console.warn('Service worker has slept');
      }
      if (periodIsPassed) {
        console.warn('Proxy update period has passed, updating premium data');
      }

      // We need to emulate onStartupListener when service worker wakes up
      await onStartupListener();
    }
  } catch (error) {
    console.error(error);
  }
}
