import { browser_api } from '@app/browser_api';
import { backgroundScope } from '../sentryScopeBackground';

export async function reloadAllYoutubeTabs(): Promise<void> {
  try {
    const tabs = await browser_api.tabs.query({
      url: '*://*.youtube.com/*',
    });

    await Promise.all(
      tabs.map((tab) => {
        if (tab.id) {
          backgroundScope.setTag('event_type', 'tab_reload');
          backgroundScope.setTag('tab_url', tab.url || '');
          backgroundScope.setTag('component', 'reloadAllYoutubeTabs');
          backgroundScope.captureMessage('Reloading tab');

          return browser_api.tabs.reload(tab.id);
        }
        return null;
      }),
    );
  } catch (error) {
    console.error('[RELOAD ALL YOUTUBE TABS] Error', error);
  }
}
