import { PremiumAlarms } from '@app/alarms/premium';
import { browser_api } from '@app/browser_api';
import { showAdPremiumBanner } from '../alarmHandlers/showAdPremiumBanner';
import { hideAdPremiumBanner } from '../alarmHandlers/hideAdPremiumBanner';
import { paymentStarted } from '../alarmHandlers/paymentStarted';
import { premiumCanceled } from '../alarmHandlers/premiumCanceled';
import { logger } from '@utils/logger';

export async function onAlarmListener(alarm: browser_api.Alarms.Alarm) {
  const name = alarm.name as PremiumAlarms;

  if (name === PremiumAlarms.ShowAdPremiumBanner) {
    logger.info('youboost:onAlarmListener ShowAdPremiumBanner');
    showAdPremiumBanner();
  }

  if (name === PremiumAlarms.HideAdPremiumBanner) {
    logger.info('youboost:onAlarmListener HideAdPremiumBanner');
    hideAdPremiumBanner();
  }

  if (name === PremiumAlarms.PaymentStarted) {
    logger.info('youboost:onAlarmListener PaymentStarted');
    paymentStarted();
  }

  if (name === PremiumAlarms.PremiumCanceled) {
    logger.info('youboost:onAlarmListener PremiumCanceled');
    premiumCanceled();
  }
}
