import { browser_api } from '@app/browser_api';
import { DetectedConflictingExtensions } from './types';

export async function detectProxyController(): Promise<DetectedConflictingExtensions> {
  try {
    const extensions = await browser_api.management.getAll();

    const proxyExtensions = extensions.filter(
      (ext) =>
        ext.id !== browser_api.runtime.id &&
        ext.enabled &&
        ext.permissions?.includes('proxy'),
    );

    if (proxyExtensions.length > 0) {
      const controllerExt = proxyExtensions[proxyExtensions.length - 1];

      const theBiggestIcon = controllerExt.icons?.reduce((acc, icon) => {
        return icon.size > acc.size ? icon : acc;
      }, controllerExt.icons?.[0]);

      return {
        hasConflictingExtension: true,
        type: 'proxy',
        conflictingExtensionId: controllerExt.id,
        conflictingExtensionName: controllerExt.name,
        conflictingExtensionIcon: theBiggestIcon?.url,
      };
    }

    return { hasConflictingExtension: false, type: 'proxy' };
  } catch (error) {
    console.error('Error detecting proxy controller:', error);
    return { hasConflictingExtension: false, type: 'proxy' };
  }
}
