import { Actions } from '@app/actions/shared';
import { browser_api } from '@app/browser_api';
import { type ShowVideoLoaderMessage } from '@app/messages/shared';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { backgroundScope } from '@background/network/sentryScopeBackground';
import { checkShouldSkipCaptcha } from '@background/redirect/utils';
import {
  BASE_URL,
  replaceTimecodeWithSavedTime,
} from '@background/urls/replaceTimecodeWithSavedTime';
import { logger } from '@utils/logger';
import { getMetaApiData } from '@utils/metaApiData';

export async function tabsOnUpdatedListener(tabId: number) {
  try {
    await getMetaApiData();

    const { currentState, status, configsBaseUrl } = await getLocalStorageData([
      LocalStorageKey.CurrentState,
      LocalStorageKey.Status,
      LocalStorageKey.ConfigsBaseUrl,
    ]);

    if (currentState !== 'connected') {
      return;
    }

    const tabs = await browser_api.tabs.query({
      active: true,
      currentWindow: true,
    });

    const activeTab = tabs.length > 0 ? tabs[0] : undefined;
    const isActiveTab = activeTab?.id === tabId;

    const isConnected = currentState === 'connected';

    if (
      activeTab &&
      activeTab.id &&
      activeTab.url &&
      (activeTab.url.includes('youtube.com') ||
        activeTab.url.includes('youtu.be'))
    ) {
      browser_api.tabs.sendMessage(activeTab.id, {
        action: Actions.UserStatusChanged,
        payload: { status },
      });
      logger.info(
        'tabsOnUpdatedListener:send UserStatusChanged message',
        activeTab.id,
      );

      browser_api.tabs.sendMessage(activeTab.id, {
        action: Actions.ConnectionStateChanged,
        payload: { connectionState: currentState },
      });
      logger.info(
        'tabsOnUpdatedListener:send ConnectionStateChanged message',
        activeTab.id,
      );

      browser_api.tabs.sendMessage(activeTab.id, {
        action: Actions.ConfigsBaseUrl,
        payload: {
          configsBaseUrl: configsBaseUrl || process.env.CONFIG_BASE_URL_1,
        },
      });
      logger.info(
        'tabsOnUpdatedListener:send ConfigsBaseUrl message',
        activeTab.id,
      );
    }

    const isCaptchaUrl =
      activeTab?.url?.includes('google.com/sorry') &&
      activeTab?.title?.includes('youtube.com/watch');

    console.warn('tabsOnUpdatedListener:isCaptchaUrl', isCaptchaUrl);

    const shouldSkipCaptcha = await checkShouldSkipCaptcha();
    console.warn('tabsOnUpdatedListener:shouldSkipCaptcha', shouldSkipCaptcha);

    if (
      !shouldSkipCaptcha &&
      activeTab?.id &&
      activeTab?.url &&
      isActiveTab &&
      isConnected &&
      isCaptchaUrl
    ) {
      // Wait for the page to be fully loaded
      await new Promise<void>((resolve) => {
        const checkLoadStatus = async () => {
          const tabStatus = await browser_api.tabs.get(activeTab.id!);
          if (tabStatus.status === 'complete') {
            resolve();
          } else {
            setTimeout(checkLoadStatus, 100);
          }
        };
        checkLoadStatus();
      });

      await browser_api.tabs.sendMessage<ShowVideoLoaderMessage>(activeTab.id, {
        action: Actions.ShowPageLoader,
      });
      logger.info(
        'tabsOnUpdatedListener:send ShowPageLoader message',
        activeTab.id,
      );

      const newUrl = await replaceTimecodeWithSavedTime(activeTab.url);

      logger.info('tabsOnUpdatedListener:tab updated started', newUrl);

      backgroundScope.setTag('event_type', 'tabs_update');
      backgroundScope.setTag('tab_utl', activeTab?.url || '');
      backgroundScope.setTag('component', 'tabsOnUpdatedListener');
      backgroundScope.captureMessage('Updating tab');

      await browser_api.tabs.update(activeTab.id, { url: newUrl });
      logger.info('tabsOnUpdatedListener:tab updated', activeTab.id);
    }

    // if the url does not include www we need to add it
    if (
      activeTab?.id === tabId &&
      activeTab.url?.startsWith('https://youtube.com') &&
      isConnected
    ) {
      const newUrl = activeTab.url.replace(
        '//youtube.com',
        '//www.youtube.com',
      );

      const urlWithTimecode = await replaceTimecodeWithSavedTime(newUrl);

      logger.info('tabsOnUpdatedListener:tab updated', tabId);

      backgroundScope.setTag('event_type', 'tabs_update');
      backgroundScope.setTag('tab_utl', activeTab?.url || '');
      backgroundScope.setTag('component', 'tabsOnUpdatedListener');
      backgroundScope.captureMessage('Updating tab');

      await browser_api.tabs.update(tabId, { url: urlWithTimecode });
      logger.info('tabsOnUpdatedListener:tab updated', tabId);
    }

    if (
      activeTab?.id === tabId &&
      activeTab.url?.startsWith('https://youtu.be') &&
      isConnected
    ) {
      const prevURL = new URL(activeTab.url);
      const params = new URLSearchParams(prevURL.search);
      const newUrl = new URL(BASE_URL); // https://www.youtube.com/watch

      for (const [key, value] of params.entries()) {
        newUrl.searchParams.set(key, value);
      }

      newUrl.searchParams.set('v', activeTab.url.split('/').pop()!);
      newUrl.searchParams.set('feature', 'youtu.be');

      const urlWithTimecode = await replaceTimecodeWithSavedTime(
        newUrl.toString(),
      );

      logger.info('tabsOnUpdatedListener:urlWithTimecode', urlWithTimecode);

      backgroundScope.setTag('event_type', 'tabs_update');
      backgroundScope.setTag('tab_utl', activeTab?.url || '');
      backgroundScope.setTag('component', 'tabsOnUpdatedListener');
      backgroundScope.captureMessage('Updating tab');

      await browser_api.tabs.update(tabId, { url: urlWithTimecode });
      logger.info('tabsOnUpdatedListener:tab updated', tabId);
    }
  } catch (error) {
    logger.error('tabsOnUpdatedListener:error', error);
    alert('error');
  }
}
