import { browser_api } from '@app/browser_api';
import { DetectedConflictingExtensions, KnownExtensions } from './types';

export async function detectAdBlockers(
  knownExtensions: KnownExtensions,
): Promise<DetectedConflictingExtensions> {
  try {
    const extensions = await browser_api.management.getAll();
    const adBlockers = extensions.filter(
      (ext) =>
        ext.id !== browser_api.runtime.id &&
        ext.enabled &&
        knownExtensions.adBlockers.some((knownExt) => knownExt.id === ext.id),
    );

    if (adBlockers.length > 0) {
      const otherAdBlocker = adBlockers[adBlockers.length - 1];

      const theBiggestIcon = otherAdBlocker.icons?.reduce((acc, icon) => {
        return icon.size > acc.size ? icon : acc;
      }, otherAdBlocker.icons?.[0]);

      return {
        hasConflictingExtension: true,
        type: 'adBlocker',
        conflictingExtensionId: otherAdBlocker.id,
        conflictingExtensionName: otherAdBlocker.name,
        conflictingExtensionIcon: theBiggestIcon?.url,
      };
    }

    return { hasConflictingExtension: false, type: 'adBlocker' };
  } catch (error) {
    console.error('Error detecting ad blockers:', error);
    return { hasConflictingExtension: false, type: 'adBlocker' };
  }
}
