import { setLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { analytics } from '@background/analytics/ga';
import { detectAdBlockers } from './detectAdBlockers';
import { detectInternalExtensions } from './detectInternalExtensions';
import { detectProxyController } from './detectProxyController';
import { detectVpnExtensions } from './detectVpnExtensions';
import { getInitialData } from './getInitialData';
import { ExtensionMonitoringData, KnownExtensions } from './types';

export const knownExtensions: KnownExtensions = getInitialData();

export async function handleExtensionMonitoring(): Promise<void> {
  const result: ExtensionMonitoringData = {
    proxy: {
      hasConflictingExtension: false,
      type: 'proxy',
    },
    internal: {
      hasConflictingExtension: false,
      type: 'internal',
    },
    vpn: {
      hasConflictingExtension: false,
      type: 'vpn',
    },
    adBlocker: {
      hasConflictingExtension: false,
      type: 'adBlocker',
    },
  };

  const proxyResult = await detectProxyController();

  if (proxyResult.hasConflictingExtension) {
    console.error(
      'Proxy might be controlled by other extension:',
      proxyResult.conflictingExtensionName || 'unknown',
    );

    result.proxy = proxyResult;

    analytics.trackUserAction(
      'proxy_controlled_by_other_extension',
      proxyResult.conflictingExtensionName || 'unknown',
      proxyResult.conflictingExtensionId,
    );
  }

  const internalResult = await detectInternalExtensions(knownExtensions);

  if (internalResult.hasConflictingExtension) {
    console.error(
      'Another internal extension detected:',
      internalResult.conflictingExtensionName || 'unknown',
    );

    result.internal = internalResult;

    analytics.trackUserAction(
      'internal_extension_conflict',
      internalResult.conflictingExtensionName || 'unknown',
      internalResult.conflictingExtensionId,
    );
  }

  const vpnResult = await detectVpnExtensions(knownExtensions);

  if (vpnResult.hasConflictingExtension) {
    console.error(
      'Another VPN extension detected:',
      vpnResult.conflictingExtensionName || 'unknown',
    );

    result.vpn = vpnResult;

    analytics.trackUserAction(
      'vpn_extension_conflict',
      vpnResult.conflictingExtensionName || 'unknown',
      vpnResult.conflictingExtensionId,
    );
  }

  const adBlockerResult = await detectAdBlockers(knownExtensions);

  if (adBlockerResult.hasConflictingExtension) {
    console.error(
      'Another ad blocker detected:',
      adBlockerResult.conflictingExtensionName || 'unknown',
    );

    result.adBlocker = adBlockerResult;

    analytics.trackUserAction(
      'ad_blocker_conflict',
      adBlockerResult.conflictingExtensionName || 'unknown',
      adBlockerResult.conflictingExtensionId,
    );

    await setLocalStorageData({
      [LocalStorageKey.IsAdBlockersWork]: true,
    });
  } else {
    await setLocalStorageData({
      [LocalStorageKey.IsAdBlockersWork]: false,
    });
  }

  await setLocalStorageData({ [LocalStorageKey.ExtensionMonitoring]: result });
}
