import { AuthDetails, FirefoxListener } from '@background/network/proxy/types';
import { handleProxyAuth } from '@background/network/proxy/proxyAuthHandler/handleProxyAuth';

export const proxyAuthListenerFirefox: FirefoxListener = async (details) => {
  const authDetails: AuthDetails = {
    challenger: details.challenger,
    isProxy: details.isProxy,
  };

  return handleProxyAuth(authDetails);
};
