import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

import { backgroundScope } from '@background/network/sentryScopeBackground';

export async function setSentryTags() {
  try {
    const { userId, deviceId } = await getLocalStorageData([
      LocalStorageKey.UserId,
      LocalStorageKey.DeviceId,
    ]);

    if (userId) {
      backgroundScope.setUser({ username: userId });
      backgroundScope.setTag('user_id', userId);
    }

    if (deviceId) {
      backgroundScope.setTag('device_id', deviceId);
      backgroundScope.setUser({ device_id: deviceId });
    }

    backgroundScope.captureEvent({
      message: 'Sentry tags set',
      tags: {
        user_id: userId,
        device_id: deviceId,
      },
    });
  } catch (error) {
    console.error('[setSentryTags] Caught error:', error);
    backgroundScope.captureException(error);
  }
}
