import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { toggleProxy } from '@background/network/proxy/proxyControl';
import { logger } from '@utils/logger';

export async function onIsAllInternetProxyChanged() {
  logger.info('shared:IsAllInternetProxyChanged');

  const { hasPremium } = await getLocalStorageData([
    LocalStorageKey.HasPremium,
  ]);

  if (hasPremium) {
    await toggleProxy();
  }
}
