import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function getIsLitePACEnabled(): Promise<boolean> {
  const { hasPremium, pacData, pacPremiumData } = await getLocalStorageData([
    LocalStorageKey.HasPremium,
    LocalStorageKey.PACData,
    LocalStorageKey.PACPremiumData,
  ]);

  let isLitePAC = false;

  if (hasPremium) {
    isLitePAC = pacPremiumData?.type === 'lite';
  } else {
    isLitePAC = pacData?.type === 'lite';
  }

  return isLitePAC;
}
