import { fetchCheckTariffAvailability } from '@api/premium/fetchPremiumData';
import { Tariff } from '@api/premium/types/Tariff';
import { browser_api } from '@app/browser_api';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { getDeviceId } from '@app/storage/utils/getDeviceId';
import { getDeviceIP } from '@app/storage/utils/getDeviceIP';
import { versionStringToNumber } from '@popup/shared/helpers/versionStringToNumber';
import { getConfigsUrl } from '@utils/getConfigsUrl';
import { getMetaApiData } from '@utils/metaApiData';

export async function openExtensionUpdateWelcomePage(
  previousVersion: string,
): Promise<void> {
  try {
    const data = await getMetaApiData();

    const prevVersion = versionStringToNumber(previousVersion);

    const showOnUpdateVersion = versionStringToNumber(
      data.showOnUpdateVersion ?? browser_api.runtime.getManifest().version,
    );

    const [{ apiBaseUrl }, deviceId, deviceIp, { userId, hasPremium }] =
      await Promise.all([
        getMetaApiData(),
        getDeviceId(),
        getDeviceIP(),
        getLocalStorageData([
          LocalStorageKey.UserId,
          LocalStorageKey.HasPremium,
        ]),
      ]);

    const isCorrectVersion = showOnUpdateVersion > prevVersion;
    const isFreeUser = userId && deviceId && !hasPremium;

    if (process.env.ON_UPDATE_URL && isCorrectVersion && isFreeUser) {
      const response = await fetchCheckTariffAvailability(apiBaseUrl, {
        userId,
        deviceId,
        deviceIp,
        tariff: Tariff.FreeTrial,
      });

      if ('error' in response) {
        // TODO: replace error handling https://vpnn.youtrack.cloud/issue/FRO-552
        throw new Error(response.error);
      }
      const { status } = response;

      if (status === 'allowed') {
        const onUpdateUrl = await getConfigsUrl(process.env.ON_UPDATE_URL);

        if (onUpdateUrl) {
          await browser_api.tabs.create({ url: onUpdateUrl.toString() });
        }
      }
    }
  } catch (err) {
    console.error(err);
  }
}
