import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';

import { browser_api } from '@app/browser_api';
import type { Types } from 'webextension-polyfill';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { handleActiveTabWhenProxyControlledByOtherExtensions } from '@background/network/handleActiveTabWhenProxyControlledByOtherExtensions';
import { handleUpdatedTabWhenProxyControlledByOtherExtensions } from '@background/network/handleUpdatedTabWhenProxyControlledByOtherExtensions';
import { logger } from '@utils/logger';

async function logProxySettings(
  details: Types.SettingOnChangeDetailsType,
): Promise<void> {
  await setLocalStorageData({
    browserProxySettings: {
      levelOfControl: details.levelOfControl,
    },
  });
}

export async function proxySettingsOnChangeListener(
  details: Types.SettingOnChangeDetailsType,
): Promise<void> {
  try {
    await logProxySettings(details);

    const { currentState } = await getLocalStorageData(
      LocalStorageKey.CurrentState,
    );

    if (currentState !== 'connected') {
      return;
    }

    if (details.levelOfControl === 'controlled_by_other_extensions') {
      const window = await browser_api.windows.getCurrent();

      await setLocalStorageData({
        [LocalStorageKey.IsProxyControlledByOtherExtensions]: true,
      });
      if (window.id) {
        await browser_api.windows.update(window.id, { focused: true });
        await browser_api.action.openPopup({ windowId: window.id });
      } else {
        if (
          browser_api.tabs.onActivated.hasListener(
            handleActiveTabWhenProxyControlledByOtherExtensions,
          )
        ) {
          browser_api.tabs.onActivated.removeListener(
            handleActiveTabWhenProxyControlledByOtherExtensions,
          );
          logger.info(
            'proxySettingsOnChangeListener:remove handleActiveTabWhenProxyControlledByOtherExtensions',
          );
        }
        browser_api.tabs.onActivated.addListener(
          handleActiveTabWhenProxyControlledByOtherExtensions,
        );
        logger.info(
          'proxySettingsOnChangeListener:add handleActiveTabWhenProxyControlledByOtherExtensions',
        );

        if (
          browser_api.tabs.onUpdated.hasListener(
            handleUpdatedTabWhenProxyControlledByOtherExtensions,
          )
        ) {
          browser_api.tabs.onUpdated.removeListener(
            handleUpdatedTabWhenProxyControlledByOtherExtensions,
          );
          logger.info(
            'proxySettingsOnChangeListener:remove handleUpdatedTabWhenProxyControlledByOtherExtensions',
          );
        }
        browser_api.tabs.onUpdated.addListener(
          handleUpdatedTabWhenProxyControlledByOtherExtensions,
        );
        logger.info(
          'proxySettingsOnChangeListener:add handleUpdatedTabWhenProxyControlledByOtherExtensions',
        );
      }
    } else if (details.levelOfControl === 'controlled_by_this_extension') {
      await setLocalStorageData({
        [LocalStorageKey.IsProxyControlledByOtherExtensions]: false,
        popupWasOpened: false,
      });
    }
  } catch (error) {
    console.error(error);
  }
}
