import { Actions } from '@app/actions/shared';
import { browser_api } from '@app/browser_api';
import { backgroundScope } from '../sentryScopeBackground';

export async function messageConnectionSuccessToAllTabs(): Promise<void> {
  try {
    const tabs = await browser_api.tabs.query({ url: '*://*.youtube.com/*' });
    await Promise.all([
      tabs.map((tab) => {
        if (tab.id) {
          backgroundScope.setTag('event_type', 'tab_connection_success_reload');
          backgroundScope.setTag('tab_url', tab.url || '');
          backgroundScope.captureMessage('Connection success / reload to tab');

          return browser_api.tabs.sendMessage(tab.id, {
            action: Actions.NetworkConnectionRetrySuccess,
          });
        }
        return null;
      }),
    ]);
  } catch (error) {
    console.error('[MESSAGE CONNECTION SUCCESS TO ALL TABS] Error', error);
  }
}
