import { browser_api } from '@app/browser_api';
import { setupSubscriptionExpiringToastMessageHandlerListener } from './messageListenersHandler';

export function setupSubscriptionExpiringToastMessageHandler(): void {
  if (
    !browser_api.runtime.onMessage.hasListener(
      setupSubscriptionExpiringToastMessageHandlerListener,
    )
  ) {
    browser_api.runtime.onMessage.addListener(
      setupSubscriptionExpiringToastMessageHandlerListener,
    );
  }
}

export function removeSubscriptionExpiringToastMessageHandler(): void {
  if (
    browser_api.runtime.onMessage.hasListener(
      setupSubscriptionExpiringToastMessageHandlerListener,
    )
  ) {
    browser_api.runtime.onMessage.removeListener(
      setupSubscriptionExpiringToastMessageHandlerListener,
    );
  }
}
