import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { ProxyDataResponse, ProxyFailReason } from '../../types/ProxyData';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { fetchPremiumProxyData } from '@api/premium/fetchPremiumProxyData';
import { fetchProxyData } from '@api/fetchProxyData';

export async function getPremiumProxyData({
  apiBaseUrl,
  deviceId,
  deviceIp,
  onFail,
  onFailReason,
}: {
  apiBaseUrl: string;
  deviceId: string;
  deviceIp?: string;
  onFail?: boolean;
  onFailReason?: ProxyFailReason;
}): Promise<ProxyDataResponse> {
  const { userId, hasPremium } = await getLocalStorageData([
    LocalStorageKey.UserId,
    LocalStorageKey.HasPremium,
  ]);

  if (userId && hasPremium) {
    const data = await fetchPremiumProxyData({
      apiBaseUrl,
      userId,
      deviceId,
      deviceIp,
      onFail,
      onFailReason,
    });

    if ('error' in data) {
      if (data.error === 'changeDevice') {
        await setLocalStorageData({
          [LocalStorageKey.IsChangeDeviceError]: true,
        });
        await setLocalStorageData({
          [LocalStorageKey.TargetState]: 'disconnected',
        });
      }
      throw new Error(data.error);
    }

    // Save last successful proxy request timestamp
    await setLocalStorageData({
      [LocalStorageKey.LastProxyRequestAt]: Date.now(),
    });

    return data;
  }

  const baseData = await fetchProxyData({
    apiBaseUrl,
    deviceId,
    deviceIp,
    onFail,
    onFailReason,
  });

  // Save last successful proxy request timestamp for base proxy as well
  await setLocalStorageData({
    [LocalStorageKey.LastProxyRequestAt]: Date.now(),
  });

  return baseData;
}
