import { Actions } from '@app/actions/shared';
import { browser_api } from '@app/browser_api';
import {
  isMessageOfType,
  ShowSubscriptionExpiringScreenMessage,
} from '@app/messages/shared';

export async function temporaryPopupListenerForBuySubscription(
  message: unknown,
): Promise<void> {
  if (isMessageOfType(message, Actions.PopupReady)) {
    await browser_api.runtime.sendMessage<ShowSubscriptionExpiringScreenMessage>(
      {
        action: Actions.ShowSubscriptionExpiringScreen,
        payload: { status: 'buy-subscription' },
      },
    );

    browser_api.runtime.onMessage.removeListener(
      temporaryPopupListenerForBuySubscription,
    );
  }
}

