import { detectBrowser } from '@app/utils/detectBrowser';
import { logger } from '@utils/logger';
import { browser_api } from '@app/browser_api';
import {
  ProxyAuthConfig,
  ProxyAuthConfigChrome,
  ProxyAuthConfigFirefox,
} from '@background/network/proxy/types';
import {
  proxyAuthListenerChrome,
  proxyAuthListenerFirefox,
} from '@background/network/proxy/proxyAuthHandler';
import { type BrowserTypes } from '@app/browser_api/types';

const PROXY_AUTH_CONFIGS: Record<BrowserTypes, ProxyAuthConfig> = {
  chrome: {
    type: 'chrome',
    listener: proxyAuthListenerChrome,
    options: { urls: ['<all_urls>'] },
    extraInfoSpec: ['asyncBlocking'],
  } as ProxyAuthConfigChrome,
  firefox: {
    type: 'firefox',
    listener: proxyAuthListenerFirefox,
    options: { urls: ['<all_urls>'] },
    extraInfoSpec: ['blocking'],
  } as ProxyAuthConfigFirefox,
};

function setupChromeProxyAuth(config: ProxyAuthConfigChrome): void {
  if (chrome.webRequest.onAuthRequired.hasListener(config.listener)) {
    chrome.webRequest.onAuthRequired.removeListener(config.listener);
    logger.info('shared:remove proxyAuthListener (chrome)');
  }

  chrome.webRequest.onAuthRequired.addListener(
    config.listener,
    config.options,
    config.extraInfoSpec,
  );

  logger.info('shared:add proxyAuthListener (chrome)');
}

function setupFirefoxProxyAuth(config: ProxyAuthConfigFirefox): void {
  if (browser_api.webRequest.onAuthRequired.hasListener(config.listener)) {
    browser_api.webRequest.onAuthRequired.removeListener(config.listener);
    logger.info('shared:remove proxyAuthListener (firefox)');
  }

  browser_api.webRequest.onAuthRequired.addListener(
    config.listener,
    config.options,
    config.extraInfoSpec,
  );

  logger.info('shared:add proxyAuthListener (firefox)');
}

export function setupProxyAuthListener(): void {
  const browser = detectBrowser();
  const config = PROXY_AUTH_CONFIGS[browser];

  if (config.type === 'chrome') {
    setupChromeProxyAuth(config);
  } else {
    setupFirefoxProxyAuth(config);
  }
}
