import { featureStore } from '@app/features/list';
import { Feature } from '@app/features/types/Feature';

export async function getIsLitePACFeatureEnabled(): Promise<boolean> {
  const featureStoreData = await featureStore.getSnapshotAsync();
  const isLitePACFeatureEnabled =
    featureStoreData.featuresList[Feature.PACLite];

  return isLitePACFeatureEnabled;
}
