import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { getPacMeta } from '@api/getPacMeta';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { handleUuidUpdate } from './utils/handleUuidUpdate';
import { handleTimeUpdate } from './utils/handleTimeUpdate';

export async function updatePacScript() {
  try {
    const { lastPacScriptUuid, pacData, pacPremiumData } =
      await getLocalStorageData([
        LocalStorageKey.LastPacScriptUuid,
        LocalStorageKey.PACData,
        LocalStorageKey.PACPremiumData,
      ]);

    const pacMetaData = await getPacMeta();

    const lastUpdatedAt = pacPremiumData?.updatedAt || pacData?.updatedAt;

    const { updatedAt, pacScriptUuid: currentUuid } = pacMetaData;

    await handleUuidUpdate(lastPacScriptUuid, currentUuid);

    await handleTimeUpdate(updatedAt, lastUpdatedAt);
  } catch (err) {
    console.error(err);
    await setLocalStorageData({ currentState: 'error' });
    throw new Error('Failed to update PAC script');
  }
}
