import { browser_api } from '@app/browser_api';
import { initStore } from '../store/appStore/initStore';
import { initIcon } from './icon/utils';
import { storageOnChangedListenerStoreTemp } from './listeners/storageOnChangedListenerStoreTemp';
import { logger } from '@utils/logger';
import { storageOnChangedListener } from './listeners/storageOnChangedListener';
import { runtimeOnInstalledListener } from './listeners/runtimeOnInstalledListener';
import { runtimeOnMessageListener } from './listeners/runtimeOnMessageListener';
import { tabsOnActivatedListener } from './listeners/tabsOnActivatedListener';
import { tabsOnUpdatedListener } from './listeners/tabsOnUpdatedListener';
import { onAlarmListener } from './listeners/onAlarmListener';
import { onStartupListener } from './listeners/onStartupListener';
import { proxySettingsOnChangeListener } from './listeners/proxySettingsOnChangeListener';
import { initOpenPopupIfError } from './utils/initOpenPopupIfError';
import { initServiceWorkerPingPong } from './utils/initServiceWorkerPingPong';
import { setupProxyAuthListener } from '@background/network/proxy/utils/setupProxyAuthListener';
import { initGiftsOnYoutubeBackground } from './gifts/giftsOnYoutubePageBackground';
import { initI18n } from '@app/i18n';

// Proxy auth listener
setupProxyAuthListener();

initServiceWorkerPingPong();

// TODO FRO-501      appStore.setState.
if (
  browser_api.storage.local.onChanged.hasListener(
    storageOnChangedListenerStoreTemp,
  )
) {
  browser_api.storage.local.onChanged.removeListener(
    storageOnChangedListenerStoreTemp,
  );
  logger.info('shared:remove storageOnChangedListenerStoreTemp');
}

browser_api.storage.local.onChanged.addListener(
  storageOnChangedListenerStoreTemp,
);
logger.info('shared:add storageOnChangedListenerStoreTemp');
// END TODO 501

// Storage listeners
if (browser_api.storage.onChanged.hasListener(storageOnChangedListener)) {
  browser_api.storage.onChanged.removeListener(storageOnChangedListener);
  logger.info('shared:remove storageOnChangedListener');
}
browser_api.storage.onChanged.addListener(storageOnChangedListener);
logger.info('shared:add storageOnChangedListener');

// Runtime listeners
// OnInstalled
if (browser_api.runtime.onInstalled.hasListener(runtimeOnInstalledListener)) {
  browser_api.runtime.onInstalled.removeListener(runtimeOnInstalledListener);
  logger.info('shared:remove runtimeOnInstalledListener');
}

browser_api.runtime.onInstalled.addListener(runtimeOnInstalledListener);
logger.info('shared:add runtimeOnInstalledListener');

// OnMessage
if (browser_api.runtime.onMessage.hasListener(runtimeOnMessageListener)) {
  browser_api.runtime.onMessage.removeListener(runtimeOnMessageListener);
  logger.info('shared:remove runtimeOnMessageListener');
}

browser_api.runtime.onMessage.addListener(runtimeOnMessageListener);
logger.info('shared:add runtimeOnMessageListener');

// Tabs listeners
// OnActivated
if (browser_api.tabs.onActivated.hasListener(tabsOnActivatedListener)) {
  browser_api.tabs.onActivated.removeListener(tabsOnActivatedListener);
  logger.info('shared:remove tabsOnActivatedListener');
}

browser_api.tabs.onActivated.addListener(tabsOnActivatedListener);
logger.info('shared:add tabsOnActivatedListener');

// OnUpdated
if (browser_api.tabs.onUpdated.hasListener(tabsOnUpdatedListener)) {
  browser_api.tabs.onUpdated.removeListener(tabsOnUpdatedListener);
  logger.info('shared:remove tabsOnUpdatedListener');
}

browser_api.tabs.onUpdated.addListener(tabsOnUpdatedListener);
logger.info('shared:add tabsOnUpdatedListener');

// Alarms listeners
if (browser_api.alarms.onAlarm.hasListener(onAlarmListener)) {
  browser_api.alarms.onAlarm.removeListener(onAlarmListener);
  logger.info('shared:remove onAlarmListener');
}

browser_api.alarms.onAlarm.addListener(onAlarmListener);
logger.info('shared:add onAlarmListener');

// OnStartup
if (browser_api.runtime.onStartup.hasListener(onStartupListener)) {
  browser_api.runtime.onStartup.removeListener(onStartupListener);
  logger.info('shared:remove onStartupListener');
}

browser_api.runtime.onStartup.addListener(onStartupListener);
logger.info('shared:add onStartupListener');

// Proxy settings listeners
if (
  browser_api.proxy.settings.onChange.hasListener(proxySettingsOnChangeListener)
) {
  browser_api.proxy.settings.onChange.removeListener(
    proxySettingsOnChangeListener,
  );
  logger.info('shared:remove proxySettingsOnChangeListener');
}

browser_api.proxy.settings.onChange.addListener(proxySettingsOnChangeListener);
logger.info('shared:add proxySettingsOnChangeListener');

browser_api.proxy.settings.get({}).then(proxySettingsOnChangeListener);
logger.info('shared:get proxySettingsOnChangeListener');

initIcon();
initStore();
initOpenPopupIfError();
initGiftsOnYoutubeBackground();

void initI18n({
  enableReact: false,
  debug: false,
  onLanguageChanged: async (language) => {
    logger.info('Background script language changed to:', language);
  },
});
