import { Alarms } from '@app/alarms/shared';
import { browser_api } from '@app/browser_api';
import { logger } from '@utils/logger';

const DELAY_IN_MINUTES = 180; // 3   
const MILLISECONDS_IN_HOUR = 3600000; // 1   

export async function handleTimeUpdate(
  updatedAt: string,
  lastUpdatedAt?: string,
): Promise<void> {
  if (!lastUpdatedAt) {
    return;
  }

  const PAC_DATA_UPDATE_DELAY_IN_MINUTES = process.env
    .PAC_DATA_UPDATE_DELAY_IN_MINUTES
    ? parseInt(process.env.PAC_DATA_UPDATE_DELAY_IN_MINUTES)
    : DELAY_IN_MINUTES;

  try {
    const updatedAtDate = new Date(updatedAt);
    const lastUpdatedAtDate = new Date(lastUpdatedAt);

    const diffInHours =
      (updatedAtDate.getTime() - lastUpdatedAtDate.getTime()) /
      MILLISECONDS_IN_HOUR;

    if (diffInHours > 1) {
      const existingAlarm = await browser_api.alarms.get(Alarms.UpdatePacData);

      if (existingAlarm) {
        logger.info('PAC alarm already active, skipping new alarm creation');
        return;
      }

      browser_api.alarms.create(Alarms.UpdatePacData, {
        delayInMinutes: PAC_DATA_UPDATE_DELAY_IN_MINUTES,
      });

      logger.info('Refetch PAC data alarm details:', {
        alarmName: Alarms.UpdatePacData,
        delayInMinutes: PAC_DATA_UPDATE_DELAY_IN_MINUTES,
        diffInHours,
        updatedAt,
        lastUpdatedAt,
      });
    }
  } catch (error) {
    console.error('Error in handleTimeUpdate:', error);
    throw error;
  }
}
