import { checkTrialAvailability } from '@api/checkTrialAvailability';
import { browser_api } from '@app/browser_api';
import { clearSessionLikeData } from '@app/features/clearSessionLikeData';
import { getDeviceId } from '@app/storage/utils/getDeviceId';
import { openExtensionPopup } from '@background/utils/openExtensionPopup';
import { getConfigsUrl } from '@utils/getConfigsUrl';
import { getMetaApiData } from '@utils/metaApiData';
import { analytics } from '../../../analytics/ga';
import { openExtensionUpdateWelcomePage } from '../utils/openExtensionUpdateWelcomePage';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { logger } from '@utils/logger';

export async function runtimeOnInstalledListener(
  details: browser_api.Runtime.OnInstalledDetailsType,
) {
  logger.info('youboost:runtimeOnInstalledListener', details);
  await clearSessionLikeData();

  if (details.reason === 'install') {
    logger.info('install');
    await getDeviceId();

    const { apiBaseUrl } = await getMetaApiData();

    const isTrialAvailable = await checkTrialAvailability(apiBaseUrl);

    const welcomeUrl = isTrialAvailable
      ? await getConfigsUrl(process.env.WELCOME_NEW_URL)
      : await getConfigsUrl(
          process.env.WELCOME_BACK_URL || process.env.WELCOME_URL,
        );

    if (welcomeUrl) {
      await browser_api.tabs.create({ url: welcomeUrl.toString() });
    }

    await openExtensionPopup();
    await analytics.trackUserAction('install', 'extension');
  }

  if (details.reason === 'update') {
    logger.info('update');
    await getDeviceId();

    await analytics.trackUserAction(
      'update',
      'extension',
      details.previousVersion || 'unknown',
    );
    await openExtensionUpdateWelcomePage(details.previousVersion || '6.1.0');

    const { hasPremium } = await getLocalStorageData(
      LocalStorageKey.HasPremium,
    );

    if (hasPremium) {
      await setLocalStorageData({ [LocalStorageKey.IsAdBannerShown]: false });
    }
  }
}
